		//content
		#container{}
		#content{}
		.content-wrap{
			&__outer{}
			&__inner{}
			.withParents-reports{
				&__area{}
				&__box{display: flex;padding: 115px 23px 0;
					.banner{display: flex;align-items: center;justify-content: center;flex-direction: column;width: 162px;height: 628px;background: url("/assets/images/with_parents_reports/bg_reports01.png") no-repeat 0 0;
						*{padding: 10px 0;}
					}
					.reports{flex:1;display: flex;flex-direction: column; padding-left: 30px;width: 786px;
						&__week{margin-top: -7px;
							.btn{
								&-week{display: block;width: 100%;height: 48px;@include font($notoecp, $font-r, 24px, 48px, transparent);background-color: transparent;border-radius: 22px;
									&.default{color: #5d9eed;}
									&.active{color: #fff;background-color: #5196ff;}
									&.disabled{color: #cbcbca;}
								}
							}
						}
						&__result{text-align: left;
							&__area{}
							&__box{position: relative;overflow: hidden;margin-top: 38px;padding-top: 34px;display: flex;flex-wrap: wrap;}
							&__lists{display: flex; width: 585px;height: 110px;padding: 17px 35px;background-color: #dfe8fb;border-radius: 17px 0 0 17px;box-sizing: border-box}
							&__list{display: flex;align-items: center;justify-content: center; margin-left: 34px;width: 76px;height: 76px;background-color: #fff;border-radius: 100%;text-align: center;
								&:nth-of-type(1){margin-left: 0;}
							}
							&__review{display: flex;align-items: center;justify-content: center; width: 200px;background-color: #c9d7f6;border-radius: 0 17px 17px 0;}
							.tit{@include font($nsroundecp, $font-b, 24px, 24px, #5d9eed);position: absolute;top: 0;left: 0;}
							.btn{
								&-review{display: inline-block;height: 63px;padding: 0 20px;@include font($nsroundecp, $font-b, 24px, 67px, #fff);border-radius: 25px;background-color: #5196ff;}
							}
							.icon{display: inline-block;
								&-answer{width: 45px;height: 31px;background: url("/assets/images/with_parents_reports/icon_check01.png") no-repeat 0 0;}
								&-wrong-answer{width: 38px;height: 36px;background: url("/assets/images/with_parents_reports/icon_close01.png") no-repeat 0 0;}
								&-incomplete{width: 42px;height: 14px;background: url("/assets/images/with_parents_reports/icon_incomplete01.png") no-repeat 0 0;}
							}
						}
						&__skill{
							&__area{}
							&__box{position: relative;margin-top: 42px;padding-top: 38px;}
							.tit{@include font($nsroundecp, $font-b, 24px, 24px, #5d9eed);position: absolute;top: 0;left: 0;}
							.data-table{overflow: hidden;border-radius: 8px 8px 0 0;
								table{width: 100%;text-align: center;}
								tr{background: url("/assets/images/with_parents_reports/bl_line_row01.png") repeat-x 0 100%;}
								th{height: 50px;@include font($notoecp, $font-r, 19px, 54px, #5d9eed);background-color: rgba(152,198,255, 0.5);}
								td{height: 90px;@include font($notoecp, $font-r, 18px, 25px, #7c7c7c);vertical-align: middle;background: url("/assets/images/with_parents_reports/bl_line_col01.png") repeat-y 0 0;
									&.td{
										&-skill{padding: 0 14px;text-align: left;background: none;}
										&-score{display: flex;align-items: center;justify-content: center;font-size: 22px;
											.score{color: #5d9eed;}
											.separator{color: #cbcbca;font-size: 22px;}
											.total{color: #cbcbca;}
										}
										&-graph{
											.graph{
												&-area{overflow: hidden;position: relative;display: inline-block;width: 180px;height: 38px;border-radius: 19px;box-sizing: border-box;vertical-align: top;background-color: #fc6492;}
												&-box{background-color: #fff;width: 100%;height: 38px;border: 3px solid #fc6492;box-sizing: border-box;border-radius: 19px;}
												&-bar{position: absolute;top: 0;left: 0;width: 100%;height: 38px;border-radius: 19px;background-color: #fc6492;transition: all 0.3s;transform: translate(-100%, 0)}
												&-txt{position: absolute;top: 2px;right: 11px;@include font($nsroundecp, $font-b, 17px, 38px, #fc6492);text-shadow: -1px -1px 0 #fff, 1px -1px 0 #fff, -1px 1px 0 #fff, 1px 1px 0 #fff;
													&.complete{color: #fff;text-shadow:none}
												}
											}
										}
									}
								}
							}
						}
						&__no-data{flex:1;display: flex;flex-direction: column;align-items: center;justify-content: center;padding-top: 30px;
							.txt1{@include font($notoecp, $font-b, 35px, 52px, #5d9eed);}
							.txt2{@include font($notoecp, $font-b, 30px, 35px, #5d9eed);}
							.btn{
								&-area{margin-top: 50px;}
								&-activity{display: inline-block;padding: 0 32px;height: 62px;@include font($nsroundecp, $font-b, 23px, 66px, #fff);background-color: #5196ff;border-radius: 33px;letter-spacing: 1px;}
							}
						}
						.swiper{position: relative;
							&-wrapper{}
							&-slide{text-align: center;width: 120px;
								&-active{}
							}
							&-controller{
								.btn-swiper-prev{display: block;position: absolute;top: -3px;left: -10px;width: 38px;height: 54px;background: url("/assets/images/with_parents_reports/icon_prev01.png") no-repeat 50% 50%;z-index: 1;
									&.swiper-button-disabled{display: none;}
								}
								.btn-swiper-next{display: block;position: absolute;top: -3px;right: -10px;width: 38px;height: 54px;background: url("/assets/images/with_parents_reports/icon_next01.png") no-repeat 50% 50%;z-index: 1;
									&.swiper-button-disabled{display: none;}
								}
							}
						}
					}
				}
			}
		}
	//}
